import React, { Fragment } from "react";
import { IoDocumentTextOutline } from "react-icons/io5";
import Modal, {
  Themes,
  ModalSize,
  ModalMarginTop,
} from "../../../components/Modal/Modal";
import { connect } from "react-redux";
import {
  Auth,
  candidateExamStoreInterface,
  SubmitExamAnswers,
  System,
  setSelectedExamQuestion,
  questionTypesStoreInterface,
} from "../../../actions";
import { StoreState } from "../../../reducers";
import { NumberOfAnsweredQuestions } from "../utils/utils";
import { course } from "../../../interfaces";
import QuestionsReview from "./QuestionsReview";
import { CountDown } from "../../../components/CountDown/CountDown";

interface TestTitleInterface {
  windowStatus: {
    profile: boolean;
    questionsList: boolean;
  };
  setProfileWindowStatus: (status: boolean) => void;
  setQuestionsListWindowStatus: (status: boolean) => void;
  setCompletedTest: (state: boolean) => void;
  SubmitExamAnswers: (user_id: string) => void;
  auth: Auth;
  candidateExam: candidateExamStoreInterface;
  system: System;
  setSelectedExamQuestion: (
    exam_id: string,
    question_id: string | null
  ) => void;
  submitQuestionAnswer: boolean;
  questionTypes: questionTypesStoreInterface;
  loadQuestionTypes: (callback: (loading: boolean) => void) => void;
  submitExam: () => void;
  SubmitAnswer: () => void;
  selectedExam: course | null;
  start_time: string;
  duration_min: number;
  timeout: () => void;
  setSubmitModal: (status: boolean) => void;
  submitModalStatus: boolean;
}

const _DoExamTitle = (props: TestTitleInterface) => {
  // const [incompleteQuestions, setIncompleteQuestions] = useState<exam_question[] | []>([]);

  const TotalAnswered: number = props.candidateExam.candidate_exam
    ? NumberOfAnsweredQuestions(
        props.candidateExam.question_answers,
        props.candidateExam.candidate_exam.questions,
        props.questionTypes
      )
    : 0;
  const answeredPercentage = props.candidateExam.candidate_exam
    ? (TotalAnswered * 100) /
      props.candidateExam.candidate_exam.questions.length
    : 0;
  return (
    <Fragment>
      <div className="bg-gray-800">
        <div className="grid grid-cols-12 md:grid-cols-6">
          <div className="col-span-6 md:col-span-2 lg:col-span-2 text-white flex flex-row justify-start items-center gap-2">
            <div className="text-white flex flex-row items-center pl-1 py-3 lg:py-0 w-full">
              <div>
                <IoDocumentTextOutline className="text-3xl ml-0" />
              </div>
              <div className="mr-3 ml-1 lg:ml-3 flex flex-col w-full">
                <span className="font-bold text-lg truncate -mb-1">
                  {props.selectedExam?.course_name}
                </span>
                <span className="text-gray-300 text-xs truncate">
                  Code: {props.selectedExam?.course_code}
                </span>
              </div>
            </div>
          </div>
          <div
            className="col-span-6 md:col-span-4 lg:col-span-4"
            style={{ alignSelf: "center" }}
          >
            <div className="grid md:grid-cols-8 lg:grid-cols-12">
              <div className="hidden col-span-3 md:flex flex-col justify-center items-center h-full gap-1">
                <div className="text-gray-200 text-sm w-full hidden lg:block">
                  Completed {TotalAnswered} of{" "}
                  {props.candidateExam.candidate_exam &&
                    props.candidateExam.candidate_exam.questions.length}
                </div>
                <div className="bg-gray-600 h-3 w-full rounded-full shadow-md hidden lg:block">
                  <div
                    className="bg-gray-400 h-3 w-60 rounded-full shadow-md"
                    style={{ width: `${answeredPercentage}%` }}
                  ></div>
                </div>
                <div className="h-auto my-2 w-full bg-gray-700 rounded-full flex justify-start items-center shadow-lg lg:hidden">
                  <div
                    className="rounded-full bg-gray-500 h-full text-xs flex items-center justify-center font-bold text-white"
                    style={{ width: `${answeredPercentage}%` }}
                  >
                    <div className="relative left-0 bg-gray-500 px-1 rounded-full">
                      {answeredPercentage.toFixed(0)}%
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-span-4 hidden lg:block h-full">
                <div className="flex flex-row justify-center items-center h-full">
                  <div className="flex flex-col text-sm justify-center items-center text-gray-400">
                    <div>
                      Start time:{" "}
                      <span className="font-bold text-gray-200">
                        {props.candidateExam.candidate_exam?.start_time}
                      </span>
                    </div>
                    <div>
                      Duration:{" "}
                      <span className="font-bold text-gray-200">
                        {props.candidateExam.candidate_exam &&
                        props.candidateExam.candidate_exam.duration / 60 > 1
                          ? (
                              props.candidateExam.candidate_exam.duration / 60
                            ).toFixed(0) + " hours"
                          : props.candidateExam.candidate_exam &&
                            (
                              props.candidateExam.candidate_exam.duration / 60
                            ).toFixed(0) + " hour"}
                        {props.candidateExam.candidate_exam &&
                          props.candidateExam.candidate_exam.duration % 60 >
                            0 &&
                          ", " +
                            (props.candidateExam.candidate_exam.duration % 60) +
                            " mins"}
                      </span>
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-span-5 bg-gray-800">
                <div className="flex flex-row justify-between md:justify-end items-center h-full gap-2">
                  <div className="flex flex-col text-xs items-end justify-start text-gray-200 pl-2 md:pl-0 w-full md:pr-2">
                    <span>Counting down</span>
                    <div className="text-lg lg:text-xl font-bold">
                      <CountDown
                        start_time={props.start_time}
                        duration_min={props.duration_min}
                        timeout={() => props.timeout()}
                        returnString={true}
                        labels
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      {props.submitModalStatus === true && (
        <Modal
          title={""}
          backDrop={true}
          theme={
            props.candidateExam.question_answers.length === 0
              ? Themes.default
              : Themes.default
          }
          close={() => props.setSubmitModal(false)}
          backDropClose={true}
          widthSizeClass={ModalSize.maxWidth}
          displayClose={false}
          padding={{ title: false }}
          marginTop={ModalMarginTop.small}
        >
          <QuestionsReview
            auth={props.auth}
            candidateExam={props.candidateExam}
            system={props.system}
            setSelectedExamQuestion={props.setSelectedExamQuestion}
            submitQuestionAnswer={props.submitQuestionAnswer}
            questionTypes={props.questionTypes}
            loadQuestionTypes={props.loadQuestionTypes}
            submitExam={props.submitExam}
            selectedExam={props.selectedExam}
            setSubmitModal={props.setSubmitModal}
            selectedLanguage={props.candidateExam.selected_language}
          />
        </Modal>
      )}
    </Fragment>
  );
};

const mapStateToProps = ({
  auth,
  candidateExam,
  system,
}: StoreState): {
  auth: Auth;
  candidateExam: candidateExamStoreInterface;
  system: System;
} => {
  return { auth: auth, candidateExam: candidateExam, system: system };
};

const DoExamTitle = connect(mapStateToProps, {
  SubmitExamAnswers,
  setSelectedExamQuestion,
})(_DoExamTitle);

export default DoExamTitle;
