import React from "react";
import { IoClose } from "react-icons/io5";
import userImage from "../../../assets/profile.png";

import { connect } from "react-redux";
import { Auth, Proctoring } from "../../../actions";
import { StoreState } from "../../../reducers";
import { DATE } from "../../../utils/functions";

interface ProfileProps {
  setProfileWindowStatus: (status: boolean) => void;
  auth: Auth;
  proctoring: Proctoring;
}

const _Profile = (props: ProfileProps) => {
  return (
    <div className="h-full bg-gray-800 border-r-4 border-gray-700">
      <div className="flex flex-col h-screen">
        <div className="bg-gray-800 pb-5">
          <div className="rounded-lg flex flex-col justify-center items-center text-center mt-1">
            <div className="w-full bg-gray-800 py-2 px-2 flex flex-row justify-between items-center">
              <span className="text-xl text-white font-bold">My profile</span>
              <div className="rounded-full flex justify-center items-center bg-gray-700 p-1">
                <IoClose
                  onClick={() => props.setProfileWindowStatus(false)}
                  className="text-white text-2xl hover:text-blue-400 cursor-pointer"
                />
              </div>
            </div>
            <div
              className="rounded-md bg-gray-600 mb-3 mx-2 flex justify-center items-center overflow-hidden"
              style={{ width: "200px", height: "200px" }}
            >
              <img src={userImage} alt="" />
            </div>
            <span className="text-gray-300 text-xs">
              {props.auth.user.fname} {props.auth.user.lname}
            </span>
            <span className="text-gray-400 text-xs">
              ID: {props.auth.user.n_id}
            </span>
          </div>
        </div>

        <div className="bg-gray-800 h-full">
          <div className="flex flex-row justify-between items-center text-white py-2 px-2 bg-gray-700">
            <span>Events</span>
            <span>{props.proctoring.events.length}</span>
          </div>
          <div
            className="flex flex-col px-2 text-xs h-screen md:h-full overflow-y-auto"
            style={{ paddingBottom: "550px", paddingTop: "10px" }}
          >
            {props.proctoring.events.length === 0 ? (
              <div className="text-white font-bold">No events found!</div>
            ) : (
              props.proctoring.events.map((item, i) => (
                <div className="flex flex-col text-gray-300 border-b py-2 border-gray-700">
                  <div
                    key={i + 1}
                    className="flex flex-row items-center my-1 gap-2"
                  >
                    <div className="bg-gray-600 text-white h-6 w-6 px-3 rounded-full flex items-center justify-center">
                      {i + 1}
                    </div>
                    <div className="flex flex-col">
                      <span>{item.event_type_id}</span>
                      <span className="font-bold">{item.description}</span>
                    </div>
                  </div>
                  <div className="flex flex-row justify-between items-center">
                    <span className="text-gray-500">
                      {DATE(item.time, "DD/MM/YYYY")}
                    </span>
                    <span className="bg-gray-600 px-2 rounded-full text-xs font-bold text-yellow-300">
                      {new Date(item.time).getHours()} :
                      {new Date(item.time).getMinutes()} :
                      {new Date(item.time).getSeconds()}
                    </span>
                  </div>
                </div>
              ))
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = ({
  auth,
  proctoring,
}: StoreState): { auth: Auth; proctoring: Proctoring } => {
  return { auth: auth, proctoring: proctoring };
};

const Profile = connect(mapStateToProps, {})(_Profile);

export default Profile;
