import React from "react";
import { FaUserCircle } from "react-icons/fa";
import { IoNotifications, IoPauseCircleOutline } from "react-icons/io5";
import { MdCheckCircle, MdChevronLeft, MdChevronRight } from "react-icons/md";
import { connect } from "react-redux";
import {
  Auth,
  loadExam,
  addExamAnswer,
  deleteExamAnswers,
  SubmitExamAnswers,
  candidateExamStoreInterface,
  System,
  setSelectedExamQuestion,
  questionTypesStoreInterface,
  Proctoring,
} from "../../../actions";
import { StoreState } from "../../../reducers";
import { findQuestionType, questionAnswered } from "../utils/utils";

interface FooterInterface {
  windowStatus: {
    profile: boolean;
    questionsList: boolean;
  };
  setQuestionsListWindowStatus: (status: boolean) => void;
  setProfileWindowStatus: (status: boolean) => void;
  auth: Auth;
  candidateExam: candidateExamStoreInterface;
  system: System;
  setSelectedExamQuestion: (
    exam_id: string,
    question_id: string | null
  ) => void;
  questionTypes: questionTypesStoreInterface;
  loadQuestionTypes: (callback: (loading: boolean) => void) => void;
  SubmitAnswer: () => void;
  submitQuestionAnswer: boolean;
  proctoring: Proctoring;
  setSubmitModal: (status: boolean) => void;
}

const _Footer = (props: FooterInterface) => {
  const findIndex = (question_id: string): number => {
    if (props.candidateExam.candidate_exam !== null) {
      for (
        let x: number = 0;
        x < props.candidateExam.candidate_exam.questions.length;
        x++
      ) {
        if (
          props.candidateExam.candidate_exam.questions[x].question_id ===
          question_id
        ) {
          return x;
        }
      }
      return 0;
    }
    return 0;
  };

  let selectedIndexPrevious: number =
    props.candidateExam.selected_question !== null &&
    props.candidateExam.candidate_exam !== null
      ? findIndex(props.candidateExam.selected_question) > 0
        ? findIndex(props.candidateExam.selected_question)
        : props.candidateExam.candidate_exam.questions.length
      : -1;

  let selectedIndexNext: number =
    props.candidateExam.selected_question !== null &&
    props.candidateExam.candidate_exam !== null
      ? findIndex(props.candidateExam.selected_question) > 0 &&
        findIndex(props.candidateExam.selected_question) <
          props.candidateExam.candidate_exam.questions.length - 1
        ? findIndex(props.candidateExam.selected_question)
        : findIndex(props.candidateExam.selected_question) === 0
        ? 0
        : -1
      : 0;

  const ConfirmSubmitAnswer = (exam_id: string, question_id: string | null) => {
    props.setSelectedExamQuestion(exam_id, question_id);
    if (props.candidateExam.candidate_exam !== null) {
      let check = props.candidateExam.candidate_exam.questions.find(
        (item) =>
          questionAnswered(
            props.candidateExam.question_answers,
            item,
            findQuestionType(item.type, props.questionTypes.questionTypes)!
          ) === true
      );
      if (check !== undefined) {
        props.SubmitAnswer();
      }
    }
  };

  return (
    <div className="fixed bottom-0 flex flex-row justify-between items-center mr-2 px-2 bg-gray-700 rounded w-full">
      {/* {console.log("Selected: ", selectedIndexNext)} */}
      <div className="flex flex-row items-center">
        <div
          onClick={() =>
            props.setProfileWindowStatus(!props.windowStatus.profile)
          }
          className={`cursor-pointer flex justify-center items-center bg-gray-700 my-2 px-1 rounded`}
        >
          <div className="group cursor-pointer text-gray-100 hover:text-blue-700 h-full py-2 pl-2 pr-3 rounded shadow-lg bg-gray-600 hover:bg-blue-200 w-max">
            <div>
              <div
                className={`${
                  props.proctoring.events.length === 0
                    ? "text-white bg-gray-900 group-hover:bg-gray-100 group-hover:text-gray-700"
                    : "bg-red-600 text-white"
                } text-xs font-bold p-1 rounded-full flex justify-center items-center shadow-lg h-6 w-6`}
                style={{ position: "relative", top: "-10px", right: "-12px" }}
              >
                {props.proctoring.events.length}
              </div>
            </div>
            {props.proctoring.events.length > 0 ? (
              <IoNotifications className="text-2xl -mt-5" />
            ) : (
              <FaUserCircle className="text-2xl -mt-5" />
            )}
          </div>
          {props.windowStatus.questionsList === true && (
            <div
              className={`${
                props.proctoring.events.length === 0
                  ? "text-gray-300"
                  : "text-yellow-500"
              } bg-gray-700  px-1 py-1 rounded text-xs ml-1 font-bold`}
            >
              {props.proctoring.events.length === 0 ? (
                <>
                  No events <br />
                  are available
                </>
              ) : (
                <>
                  New events <br />
                  are available
                </>
              )}
            </div>
          )}
        </div>

        <button
          onClick={() =>
            props.candidateExam.candidate_exam &&
            props.candidateExam.selected_exam_id &&
            ConfirmSubmitAnswer(
              props.candidateExam.candidate_exam.exam_paper_id,
              props.candidateExam.candidate_exam.questions[
                selectedIndexPrevious - 1
              ].question_id
            )
          }
          className="btn btn-blue flex justify-center items-center"
          style={{ height: "fit-content" }}
        >
          <MdChevronLeft className="text-xl" />{" "}
          <span className="hidden md:block">Previous</span>
        </button>

        <div className="hidden md:flex flex-col text-gray-400 px-2">
          <span className="font-bold">
            Question{" "}
            {props.candidateExam.selected_question &&
              findIndex(props.candidateExam.selected_question) + 1}
          </span>
          {props.submitQuestionAnswer === true ? (
            <span className="text-xs text-yellow-500 animate-pulse font-bold">
              Saving question answer...
            </span>
          ) : (
            <span className="text-xs">Auto save is activated</span>
          )}
        </div>
      </div>
      <div className="flex flex-row gap-1">
        {/* <div className="px-3 py-2 rounded hover:bg-gray-800 shadow-lg cursor-pointer bg-gray-600 text-white flex items-center"><BiChat className="text-2xl" />&nbsp; <span className="hidden md:block">Discussion</span></div> */}
        <button
          onClick={() =>
            props.candidateExam.candidate_exam &&
            props.candidateExam.selected_exam_id &&
            ConfirmSubmitAnswer(
              props.candidateExam.candidate_exam.exam_paper_id,
              props.candidateExam.candidate_exam.questions[
                selectedIndexNext + 1
              ].question_id
            )
          }
          className="btn btn-blue flex items-center px-2 py-2 my-1"
        >
          <span className="hidden md:block">Next</span>{" "}
          <MdChevronRight className="text-xl" />
        </button>
        {props.submitQuestionAnswer === true ? (
          <div className="btn bg-gray-600 text-white px-2 py-2 my-1 font-bold border border-gray-600 animate-pulse">
            Saving...
          </div>
        ) : (
          <button
            onClick={() => {
              props.SubmitAnswer();
              props.setSubmitModal(true);
            }}
            type="button"
            className="bg-green-700 hover:bg-green-600 border border-white text-white rounded flex flex-row justify-center items-center gap-2 px-2 py-2 my-1 mx-2 float-right"
          >
            <MdCheckCircle className="text-white text-xl" />
            <span className="mr-2">Submit </span>
          </button>
        )}
      </div>
    </div>
  );
};

const mapStateToProps = ({
  auth,
  doExam,
  candidateExam,
  system,
  proctoring,
}: StoreState): {
  auth: Auth;
  candidateExam: candidateExamStoreInterface;
  system: System;
  proctoring: Proctoring;
} => {
  return {
    auth: auth,
    candidateExam: candidateExam,
    system: system,
    proctoring: proctoring,
  };
};

const Footer = connect(mapStateToProps, {
  loadExam,
  addExamAnswer,
  deleteExamAnswers,
  SubmitExamAnswers,
  setSelectedExamQuestion,
})(_Footer);

export default Footer;
