import React from "react";
import { MdCheckCircleOutline, MdHighlightOff } from "react-icons/md";
import { InitialExamDetails } from "../../actions/candidate_exam";

interface ProctoringRuleItemProps {
  examDetails: () => InitialExamDetails | null;
  rule_key: string;
}

const ProctoringRuleItem = (props: ProctoringRuleItemProps) => {
  const getDetails = () => {
    let res: {
      value: string;
      status: boolean;
    } | null = null;
    if (props.examDetails() !== null) {
      switch (props.rule_key) {
        case "right_click_enabled":
          res = {
            value: "Right click",
            status: props.examDetails()!.rules.right_click_enabled,
          };
          break;
        case "copy_enabled":
          res = {
            value: "Copy text",
            status: props.examDetails()!.rules.copy_enabled,
          };
          break;
        case "paste_enabled":
          res = {
            value: "Paste item or text",
            status: props.examDetails()!.rules.paste_enabled,
          };
          break;
        case "stop_candidate_when_comeback":
          res = {
            value: "Coming back after leaving",
            status: props.examDetails()!.rules.stop_candidate_when_comeback,
          };
          break;
        case "allow_to_leave_browser":
          res = {
            value: "Leave browser",
            status: props.examDetails()!.rules.allow_to_leave_browser,
          };
          break;
      }
    }
    return res;
  };
  return (
    <div className="flex flex-row items-center justify-between w-full border-b py-2">
      <div
        className={`flex flex-row items-center ${
          getDetails()?.status === true
            ? "text-blue-600 font-bold"
            : "text-yellow-800 font-bold"
        }`}
      >
        {getDetails()?.status === true ? (
          <MdCheckCircleOutline className="text-2xl mr-3" />
        ) : (
          <MdHighlightOff className="text-2xl mr-3" />
        )}
        <span>{getDetails()?.value}</span>
      </div>
      <span
        className={`rounded-full px-3 py-1 border font-bold ${
          getDetails()?.status === true
            ? "bg-white text-blue-800 border-blue-100"
            : "bg-yellow-50 text-yellow-800 border-yellow-100"
        }`}
      >
        {getDetails()?.status === true ? "Allowed" : "Prohibited"}
      </span>
    </div>
  );
};

export default ProctoringRuleItem;
