import React, { useEffect, useState } from "react";
import { MdRadioButtonChecked, MdRadioButtonUnchecked } from "react-icons/md";
import {
  QuestionAnswerFormat,
  QuestionAnswerInterface,
  QuestionInterface,
  QuestionItemInterface,
  QuestionLanguage,
  QuestionLanguages,
} from "../../../interfaces/index";
import { newAnswer } from "../DoExam";
import { connect } from "react-redux";
import {
  Auth,
  candidateExamStoreInterface,
  examDataInterface,
  System,
  setSelectedExamQuestion,
  setQuestionLanguage,
  questionTypesStoreInterface,
  loadQuestionTypes,
  QuestionAnswersStore,
} from "../../../actions";
import { StoreState } from "../../../reducers";
// import { exam_question } from "../../../interfaces/exam_question";
import Loading from "../../../components/Loading/Loading";
import { LoadingType } from "../../../components/Loading/LoadingV2";
import MultipleChoice from "./Answers/MultipleChoice";
import {
  toCalculatedMultipleChoice,
  toEssay,
  toMatching,
  toMultipleChoice,
  toNumerical,
  toShortAnswer,
  toSimpleCalculated,
  toTrueFalse,
} from "./ConvertingAnswers";
import { findQuestionType } from "../utils/utils";
import TrueFalse from "./Answers/TrueFalse";
import Matching from "./Answers/Matching";
import SimpleCalculated from "./Answers/SimpleCalculated";
import ShortAnswer from "./Answers/ShortAnswer";
import Numerical from "./Answers/Numerical";
import CalculatedMultipleChoice from "./Answers/CalculatedMultipleChoice";
import { BiWorld } from "react-icons/bi";
import { AiFillCaretDown } from "react-icons/ai";
import { FaTimes } from "react-icons/fa";
import { Shuffle } from "../../../utils/functions";
import { questionTypeElementsType } from "@mastrecruit/common";
import { exam_question } from "../../../interfaces/exam_question";
import { EmbeddedQuestion } from "../../../components/QuestionPreview/QuestionPreview";
import Essay from "./Answers/Essay";
import HybridAnswers from "./Answers/HybridAnswers";

interface QuestionDetailsProps {
  selectedQuestion: exam_question | null;
  setSelectedQuestion: (question: exam_question) => void;
  answerQuestionFn: (answer: QuestionAnswersStore) => void;
  questionAnswers: newAnswer[] | undefined;
  doExam: examDataInterface;
  auth: Auth;
  candidateExam: candidateExamStoreInterface;
  system: System;
  setSelectedExamQuestion: (
    exam_id: string,
    question_id: string | null
  ) => void;
  setQuestionLanguage: (lang: QuestionLanguage) => void;
  questionTypes: questionTypesStoreInterface;
  loadQuestionTypes: (callback: (loading: boolean) => void) => void;
  preview: boolean;
  submitQuestionAnswer: boolean;
}

const _QuestionDetails = (props: QuestionDetailsProps) => {
  const [loading, setLoading] = useState<boolean>(false);
  const [selectLang, setSelectLang] = useState<boolean>(false);
  const QuestionData = (): QuestionInterface | null => {
    let res = null;
    if (props.candidateExam.candidate_exam !== null) {
      let q: QuestionInterface | undefined =
        props.candidateExam.candidate_exam.questions.find(
          (itm) =>
            props.candidateExam.selected_question &&
            itm.question_id === props.candidateExam.selected_question &&
            itm.question.find(
              (testLang) => testLang.language === getDefaultLanguage()
            ) !== undefined
        );
      if (q !== undefined) {
        res = q;
      }
    }
    return res;
  };

  const findSelectedQuestionDetails = (): QuestionItemInterface[] => {
    let res: QuestionItemInterface[] = [];
    if (props.candidateExam.candidate_exam) {
      let x = props.candidateExam.candidate_exam.questions.find(
        (itm) => itm.question_id === props.candidateExam.selected_question
      );
      if (x !== undefined) {
        res = x.question;
      }
    }
    return res;
  };

  const filterLanguages = () => {
    let langs: {
      key: QuestionLanguage;
      value: string;
    }[] = [];
    const selectedQuestionDetails =
      props.candidateExam.candidate_exam?.questions.find(
        (itm) => itm.question_id === props.candidateExam.selected_question
      );
    if (selectedQuestionDetails !== undefined) {
      for (let langItem of selectedQuestionDetails.question) {
        let testLangQ = QuestionLanguages.find(
          (l) => l.key === langItem.language
        );
        if (testLangQ !== undefined) {
          langs = [...langs, testLangQ];
        }
      }
    }
    return langs;
  };
  const getDefaultLanguage = (): QuestionLanguage => {
    let questionSelected = props.candidateExam.candidate_exam?.questions.find(
      (itm) => itm.question_id === props.candidateExam.selected_question
    );
    let check = questionSelected?.question.find(
      (item) => item.language === props.candidateExam.selected_language
    );
    if (check === undefined && questionSelected !== undefined) {
      return questionSelected.question[0].language;
    }
    return props.candidateExam.selected_language;
  };

  // Submit answer function
  const addAnswerFunction = (data: QuestionAnswerInterface) => {
    let checkAnswerAvail = props.candidateExam.question_answers.find(
      (itm) => itm.question_id === QuestionData()!.question_id
    );
    //
    // let newData = data.answer;
    // if (checkAnswerAvail !== undefined) {
    //   let newDataAnswer:QuestionAnswerInterface | undefined = checkAnswerAvail.answers.find(itm => itm._id === data._id);
    //   if (newDataAnswer !== undefined) {

    //   }
    // }

    console.log("checkAnswerAvail a: ", checkAnswerAvail);
    console.log("data a: ", data);

    props.answerQuestionFn({
      question_id: QuestionData()!.question_id,
      status: false, //Check if submitted online
      answers:
        checkAnswerAvail === undefined
          ? [data]
          : [
              ...checkAnswerAvail.answers.filter(
                (itm) => itm.reference !== data.reference
              ),
              data,
            ],
      language: props.candidateExam.selected_language,
    });
  };
  useEffect(() => {
    if (
      props.questionTypes.questionTypes === null ||
      props.questionTypes.questionTypes.length === 0
    ) {
      props.loadQuestionTypes(setLoading);
    }
  }, [props]);
  return (
    <div className="bg-gray-800 h-full">
      {loading === true ? (
        <div className="bg-gray-200 px-3 py-2 rounded my-2 mr-2 h-full overflow-y-auto pb-80 md:pb-60">
          <div className="rounded bg-white px-4 py-4 mt-5 mx-4">
            <div className="text-2xl mx-4 mb-3">Loading...</div>
            <Loading type={LoadingType.white} />
          </div>
        </div>
      ) : (
        <div className="bg-gray-200 px-3 py-2 rounded my-2 mr-2 h-full overflow-y-auto pb-80 md:pb-60">
          <div className="flex flex-row gap-2 mt-2">
            <div className="flex flex-col md:flex-row md:justify-between w-full">
              {QuestionData() !== null &&
                QuestionData()?.question.find(
                  (itm) => itm.language === getDefaultLanguage()
                ) && (
                  <div className="text-md flex flex-col w-full">
                    <div className="flex flex-row justify-between mb-2">
                      <div className="flex flex-row gap-2 items-center justify-between w-full">
                        <span className="text-sm text-gray-600">
                          {QuestionData() !== null &&
                            QuestionData()?.question.find(
                              (itm) => itm.language === getDefaultLanguage()
                            )?.title}
                        </span>
                        <div className="text-sm bg-blue-600  rounded-full font-bold px-2 py-1 mx-2 my-1 text-white">
                          <span className="font-extrabold text-white">
                            {QuestionData() !== null && QuestionData()?.marks}
                          </span>{" "}
                          Mark(s)
                        </div>
                      </div>
                      {/* Languages */}
                      <div className="bg-white rounded h-10 hover:bg-white cursor-pointer w-max">
                        <div
                          onClick={() => setSelectLang(!selectLang)}
                          className="flex flex-row items-center gap-2"
                        >
                          <div className="rounded-l">
                            <BiWorld className="text-2xl my-2 ml-2" />
                          </div>
                          <div
                            className="flex flex-col"
                            style={{ width: "97px" }}
                          >
                            <span className="text-xs font-bold italic text-gray-700">
                              Language
                            </span>
                            <span
                              title={
                                filterLanguages().find(
                                  (itm) => itm.key === getDefaultLanguage()
                                )?.value
                              }
                              className="text-sm font-bold text-blue-600 truncate"
                              style={{ width: "97px" }}
                            >
                              {
                                filterLanguages().find(
                                  (itm) => itm.key === getDefaultLanguage()
                                )?.value
                              }
                            </span>
                          </div>
                          <div
                            className={`${
                              selectLang === true
                                ? "bg-yellow-800 text-white"
                                : "bg-gray-50 hover:bg-blue-600"
                            } h-10 rounded-r border-l border-gray-300 flex flex-row items-center justify-center cursor-pointer hover:text-white hover:border-white`}
                          >
                            {selectLang === true ? (
                              <FaTimes className="text-xl my-2 mx-2" />
                            ) : (
                              <AiFillCaretDown className="text-xl mb-1 mt-2 mx-2" />
                            )}
                          </div>
                        </div>
                        {selectLang === true && (
                          <div
                            className="relative rounded overflow-hidden"
                            style={{ width: "182px" }}
                          >
                            <div className="bg-blue-700 animate__animated animate__fadeIn animate__faster rounded-b w-full right-0 flex flex-col">
                              {filterLanguages().map((item, i) => (
                                <span
                                  onClick={() => {
                                    props.setQuestionLanguage(item.key);
                                    setSelectLang(!selectLang);
                                  }}
                                  key={i + 1}
                                  className={`flex flex-row items-center animate__animated animate__fadeInDown animate__faster text-white text-sm px-2 py-2 border-2 ${
                                    getDefaultLanguage() === item.key
                                      ? "bg-blue-800 font-extrabold border-blue-800"
                                      : "bg-blue-500 text-blue-100 border-blue-500 hover:bg-blue-700 hover:border-blue-700"
                                  }`}
                                >
                                  {getDefaultLanguage() === item.key ? (
                                    <MdRadioButtonChecked className="text-xl mr-2" />
                                  ) : (
                                    <MdRadioButtonUnchecked className="text-xl mr-2" />
                                  )}{" "}
                                  {item.value}
                                </span>
                              ))}
                            </div>
                          </div>
                        )}
                      </div>
                    </div>
                    {QuestionData() !== null &&
                    findQuestionType(
                      QuestionData()!.type,
                      props.questionTypes.questionTypes
                    ) !== null &&
                    findQuestionType(
                      QuestionData()!.type,
                      props.questionTypes.questionTypes
                    ) === questionTypeElementsType.Embedded ? (
                      EmbeddedQuestion(
                        QuestionData()!,
                        props.questionTypes.questionTypes,
                        getDefaultLanguage(),
                        props.candidateExam.question_answers,
                        addAnswerFunction
                      )
                    ) : (
                      <div
                        dangerouslySetInnerHTML={{
                          __html: QuestionData()!.question.find(
                            (itm) => itm.language === getDefaultLanguage()
                          )!.description,
                        }}
                        className="text-lg text-gray-800 font-bold"
                      ></div>
                    )}
                  </div>
                )}
            </div>
          </div>
          <div
            className={`my-4 py-2 px-2 ${
              findQuestionType(
                props.candidateExam.candidate_exam?.questions.find(
                  (itm) =>
                    itm.question_id === props.candidateExam.selected_question
                )?.type,
                props.questionTypes.questionTypes
              ) !== null &&
              findQuestionType(
                props.candidateExam.candidate_exam?.questions.find(
                  (itm) =>
                    itm.question_id === props.candidateExam.selected_question
                )?.type,
                props.questionTypes.questionTypes
              ) === questionTypeElementsType.Embedded
                ? ""
                : "bg-white"
            }  rounded`}
            onClick={() => selectLang === true && setSelectLang(false)}
          >
            {/* Single and Multiple choice */}
            {props.candidateExam.selected_question !== null &&
              findQuestionType(
                props.candidateExam.candidate_exam?.questions.find(
                  (itm) =>
                    itm.question_id === props.candidateExam.selected_question
                )?.type,
                props.questionTypes.questionTypes
              ) !== null &&
              (findQuestionType(
                props.candidateExam.candidate_exam?.questions.find(
                  (itm) =>
                    itm.question_id === props.candidateExam.selected_question
                )?.type,
                props.questionTypes.questionTypes
              ) === questionTypeElementsType.MultipleChoice ||
                findQuestionType(
                  props.candidateExam.candidate_exam?.questions.find(
                    (itm) =>
                      itm.question_id === props.candidateExam.selected_question
                  )?.type,
                  props.questionTypes.questionTypes
                ) === questionTypeElementsType.SingleMultiple) && (
                <MultipleChoice
                  type={findQuestionType(
                    props.candidateExam.candidate_exam?.questions.find(
                      (itm) =>
                        itm.question_id ===
                        props.candidateExam.selected_question
                    )?.type,
                    props.questionTypes.questionTypes
                  )}
                  available_answers={props.candidateExam.question_answers}
                  selectedQuestionAnswer={{
                    question_id: props.candidateExam.selected_question,
                    question:
                      QuestionData() !== null
                        ? QuestionData()?.question.find(
                            (itm) => itm.language === getDefaultLanguage()
                          )
                        : undefined,
                    answers: toMultipleChoice(
                      findSelectedQuestionDetails().find(
                        (itm) => itm.language === getDefaultLanguage()
                      )!.answers[0].answer
                    ),
                    selectedAns: findSelectedQuestionDetails().find(
                      (itm) => itm.language === getDefaultLanguage()
                    )!.answers[0],
                  }}
                  answerQuestionFn={(answer: QuestionAnswersStore) => {
                    addAnswerFunction(answer.answers[0]);
                  }}
                />
              )}
            {/* True and False */}
            {props.candidateExam.selected_question !== null &&
              findQuestionType(
                props.candidateExam.candidate_exam?.questions.find(
                  (itm) =>
                    itm.question_id === props.candidateExam.selected_question
                )?.type,
                props.questionTypes.questionTypes
              ) !== null &&
              findQuestionType(
                props.candidateExam.candidate_exam?.questions.find(
                  (itm) =>
                    itm.question_id === props.candidateExam.selected_question
                )?.type,
                props.questionTypes.questionTypes
              ) === questionTypeElementsType.TrueFalse && (
                <TrueFalse
                  type={findQuestionType(
                    props.candidateExam.candidate_exam?.questions.find(
                      (itm) =>
                        itm.question_id ===
                        props.candidateExam.selected_question
                    )?.type,
                    props.questionTypes.questionTypes
                  )}
                  available_answers={props.candidateExam.question_answers}
                  selectedQuestionAnswer={{
                    question_id: props.candidateExam.selected_question,
                    question:
                      QuestionData() !== null
                        ? QuestionData()?.question.find(
                            (itm) => itm.language === getDefaultLanguage()
                          )
                        : undefined,
                    answers: toTrueFalse(
                      findSelectedQuestionDetails().find(
                        (itm) => itm.language === getDefaultLanguage()
                      )!.answers[0].answer
                    ),
                    selectedAns: findSelectedQuestionDetails().find(
                      (itm) => itm.language === getDefaultLanguage()
                    )!.answers[0],
                  }}
                  answerQuestionFn={(answer: QuestionAnswersStore) => {
                    addAnswerFunction(answer.answers[0]);
                  }}
                />
              )}
            {/* Matching Question */}
            {/* {console.log(
              "Type: ",
              findQuestionType(
                props.candidateExam.candidate_exam?.questions.find(
                  (itm) =>
                    itm.question_id === props.candidateExam.selected_question
                )?.type,
                props.questionTypes.questionTypes
              )
            )} */}
            {props.candidateExam.selected_question !== null &&
              findQuestionType(
                props.candidateExam.candidate_exam?.questions.find(
                  (itm) =>
                    itm.question_id === props.candidateExam.selected_question
                )?.type,
                props.questionTypes.questionTypes
              ) !== null &&
              findQuestionType(
                props.candidateExam.candidate_exam?.questions.find(
                  (itm) =>
                    itm.question_id === props.candidateExam.selected_question
                )?.type,
                props.questionTypes.questionTypes
              ) === questionTypeElementsType.Matching && (
                <Matching
                  selectedLanguage={getDefaultLanguage()}
                  selectedExam={props.candidateExam.candidate_exam}
                  type={findQuestionType(
                    props.candidateExam.candidate_exam?.questions.find(
                      (itm) =>
                        itm.question_id ===
                        props.candidateExam.selected_question
                    )?.type,
                    props.questionTypes.questionTypes
                  )}
                  available_answers={props.candidateExam.question_answers}
                  selectedQuestionAnswer={{
                    question_id: props.candidateExam.selected_question,
                    question:
                      QuestionData() !== null
                        ? QuestionData()?.question.find(
                            (itm) => itm.language === getDefaultLanguage()
                          )
                        : undefined,
                    answers: toMatching(
                      findSelectedQuestionDetails().find(
                        (itm) => itm.language === getDefaultLanguage()
                      )!.answers[0].answer
                    ),
                    shuffledAnswers: Shuffle(
                      toMatching(
                        findSelectedQuestionDetails().find(
                          (itm) => itm.language === getDefaultLanguage()
                        )!.answers[0].answer
                      )
                    ),
                    selectedAns: findSelectedQuestionDetails().find(
                      (itm) => itm.language === getDefaultLanguage()
                    )!.answers[0],
                  }}
                  answerQuestionFn={(answer: QuestionAnswersStore) => {
                    addAnswerFunction(answer.answers[0]);
                  }}
                />
              )}
            {/* Simple Calculated */}
            {props.candidateExam.selected_question !== null &&
              findQuestionType(
                props.candidateExam.candidate_exam?.questions.find(
                  (itm) =>
                    itm.question_id === props.candidateExam.selected_question
                )?.type,
                props.questionTypes.questionTypes
              ) !== null &&
              findQuestionType(
                props.candidateExam.candidate_exam?.questions.find(
                  (itm) =>
                    itm.question_id === props.candidateExam.selected_question
                )?.type,
                props.questionTypes.questionTypes
              ) === questionTypeElementsType.SimpleCalculate && (
                <SimpleCalculated
                  type={findQuestionType(
                    props.candidateExam.candidate_exam?.questions.find(
                      (itm) =>
                        itm.question_id ===
                        props.candidateExam.selected_question
                    )?.type,
                    props.questionTypes.questionTypes
                  )}
                  available_answers={props.candidateExam.question_answers}
                  selectedQuestionAnswer={{
                    question_id: props.candidateExam.selected_question,
                    question:
                      QuestionData() !== null
                        ? QuestionData()?.question.find(
                            (itm) => itm.language === getDefaultLanguage()
                          )
                        : undefined,
                    answers: toSimpleCalculated(
                      findSelectedQuestionDetails().find(
                        (itm) => itm.language === getDefaultLanguage()
                      )!.answers[0].answer
                    ),
                    selectedAns: findSelectedQuestionDetails().find(
                      (itm) => itm.language === getDefaultLanguage()
                    )!.answers[0],
                  }}
                  answerQuestionFn={(answer: QuestionAnswersStore) => {
                    addAnswerFunction(answer.answers[0]);
                  }}
                />
              )}
            {/* Short Answer */}
            {props.candidateExam.selected_question !== null &&
              findQuestionType(
                props.candidateExam.candidate_exam?.questions.find(
                  (itm) =>
                    itm.question_id === props.candidateExam.selected_question
                )?.type,
                props.questionTypes.questionTypes
              ) !== null &&
              findQuestionType(
                props.candidateExam.candidate_exam?.questions.find(
                  (itm) =>
                    itm.question_id === props.candidateExam.selected_question
                )?.type,
                props.questionTypes.questionTypes
              ) === questionTypeElementsType.ShortAnswer && (
                <ShortAnswer
                  type={findQuestionType(
                    props.candidateExam.candidate_exam?.questions.find(
                      (itm) =>
                        itm.question_id ===
                        props.candidateExam.selected_question
                    )?.type,
                    props.questionTypes.questionTypes
                  )}
                  available_answers={props.candidateExam.question_answers}
                  selectedQuestionAnswer={{
                    question_id: props.candidateExam.selected_question,
                    question:
                      QuestionData() !== null
                        ? QuestionData()?.question.find(
                            (itm) => itm.language === getDefaultLanguage()
                          )
                        : undefined,
                    answers: toShortAnswer(
                      findSelectedQuestionDetails().find(
                        (itm) => itm.language === getDefaultLanguage()
                      )!.answers.length === 0
                        ? []
                        : findSelectedQuestionDetails().find(
                            (itm) => itm.language === getDefaultLanguage()
                          )!.answers[0].answer
                    ),
                    selectedAns: findSelectedQuestionDetails().find(
                      (itm) => itm.language === getDefaultLanguage()
                    )!.answers[0],
                  }}
                  answerQuestionFn={(answer: QuestionAnswersStore) => {
                    addAnswerFunction(answer.answers[0]);
                  }}
                />
              )}
            {/* Numerical Answer */}
            {props.candidateExam.selected_question !== null &&
              findQuestionType(
                props.candidateExam.candidate_exam?.questions.find(
                  (itm) =>
                    itm.question_id === props.candidateExam.selected_question
                )?.type,
                props.questionTypes.questionTypes
              ) !== null &&
              findQuestionType(
                props.candidateExam.candidate_exam?.questions.find(
                  (itm) =>
                    itm.question_id === props.candidateExam.selected_question
                )?.type,
                props.questionTypes.questionTypes
              ) === questionTypeElementsType.Numeric && (
                <Numerical
                  type={findQuestionType(
                    props.candidateExam.candidate_exam?.questions.find(
                      (itm) =>
                        itm.question_id ===
                        props.candidateExam.selected_question
                    )?.type,
                    props.questionTypes.questionTypes
                  )}
                  available_answers={props.candidateExam.question_answers}
                  selectedQuestionAnswer={{
                    question_id: props.candidateExam.selected_question,
                    question:
                      QuestionData() !== null
                        ? QuestionData()?.question.find(
                            (itm) => itm.language === getDefaultLanguage()
                          )
                        : undefined,
                    answers: toNumerical(
                      findSelectedQuestionDetails().find(
                        (itm) => itm.language === getDefaultLanguage()
                      )!.answers[0].answer
                    ),
                    selectedAns: findSelectedQuestionDetails().find(
                      (itm) => itm.language === getDefaultLanguage()
                    )!.answers[0],
                  }}
                  answerQuestionFn={(answer: QuestionAnswersStore) => {
                    addAnswerFunction(answer.answers[0]);
                  }}
                />
              )}
            {/* Calculated Multiple choice */}
            {props.candidateExam.selected_question !== null &&
              findQuestionType(
                props.candidateExam.candidate_exam?.questions.find(
                  (itm) =>
                    itm.question_id === props.candidateExam.selected_question
                )?.type,
                props.questionTypes.questionTypes
              ) !== null &&
              findQuestionType(
                props.candidateExam.candidate_exam?.questions.find(
                  (itm) =>
                    itm.question_id === props.candidateExam.selected_question
                )?.type,
                props.questionTypes.questionTypes
              ) === questionTypeElementsType.CalculatedMultipleChoice && (
                <CalculatedMultipleChoice
                  type={findQuestionType(
                    props.candidateExam.candidate_exam?.questions.find(
                      (itm) =>
                        itm.question_id ===
                        props.candidateExam.selected_question
                    )?.type,
                    props.questionTypes.questionTypes
                  )}
                  available_answers={props.candidateExam.question_answers}
                  selectedQuestionAnswer={{
                    question_id: props.candidateExam.selected_question,
                    question:
                      QuestionData() !== null
                        ? QuestionData()?.question.find(
                            (itm) => itm.language === getDefaultLanguage()
                          )
                        : undefined,
                    answers: toCalculatedMultipleChoice(
                      findSelectedQuestionDetails().find(
                        (itm) => itm.language === getDefaultLanguage()
                      )!.answers[0].answer
                    ),
                    selectedAns: findSelectedQuestionDetails().find(
                      (itm) => itm.language === getDefaultLanguage()
                    )!.answers[0],
                  }}
                  answerQuestionFn={(answer: QuestionAnswersStore) => {
                    addAnswerFunction(answer.answers[0]);
                  }}
                />
              )}
            {/* Essay question */}
            {props.candidateExam.selected_question !== null &&
              findQuestionType(
                props.candidateExam.candidate_exam?.questions.find(
                  (itm) =>
                    itm.question_id === props.candidateExam.selected_question
                )?.type,
                props.questionTypes.questionTypes
              ) !== null &&
              findQuestionType(
                props.candidateExam.candidate_exam?.questions.find(
                  (itm) =>
                    itm.question_id === props.candidateExam.selected_question
                )?.type,
                props.questionTypes.questionTypes
              ) === questionTypeElementsType.Essay && (
                <Essay
                  type={findQuestionType(
                    props.candidateExam.candidate_exam?.questions.find(
                      (itm) =>
                        itm.question_id ===
                        props.candidateExam.selected_question
                    )?.type,
                    props.questionTypes.questionTypes
                  )}
                  available_answers={props.candidateExam.question_answers}
                  selectedQuestionAnswer={{
                    question_id: props.candidateExam.selected_question,
                    question:
                      QuestionData() !== null
                        ? QuestionData()?.question.find(
                            (itm) => itm.language === getDefaultLanguage()
                          )
                        : undefined,
                    answers: toEssay(
                      findSelectedQuestionDetails().find(
                        (itm) => itm.language === getDefaultLanguage()
                      )!.answers[0].answer
                    ),
                    selectedAns: findSelectedQuestionDetails().find(
                      (itm) => itm.language === getDefaultLanguage()
                    )!.answers[0],
                  }}
                  answerQuestionFn={(answer: QuestionAnswersStore) => {
                    addAnswerFunction(answer.answers[0]);
                  }}
                />
              )}

            {/* Hybrid answer unique: it combines th above questions */}
            {props.candidateExam.selected_question !== null &&
              findQuestionType(
                props.candidateExam.candidate_exam?.questions.find(
                  (itm) =>
                    itm.question_id === props.candidateExam.selected_question
                )?.type,
                props.questionTypes.questionTypes
              ) !== null &&
              findQuestionType(
                props.candidateExam.candidate_exam?.questions.find(
                  (itm) =>
                    itm.question_id === props.candidateExam.selected_question
                )?.type,
                props.questionTypes.questionTypes
              ) === questionTypeElementsType.Hybrid && (
                <HybridAnswers
                  type={findQuestionType(
                    props.candidateExam.candidate_exam?.questions.find(
                      (itm) =>
                        itm.question_id ===
                        props.candidateExam.selected_question
                    )?.type,
                    props.questionTypes.questionTypes
                  )}
                  available_answers={props.candidateExam.question_answers}
                  selectedQuestionAnswer={{
                    question_id: props.candidateExam.selected_question,
                    question:
                      QuestionData() !== null
                        ? QuestionData()?.question.find(
                            (itm) => itm.language === getDefaultLanguage()
                          )
                        : undefined,
                    answers: findSelectedQuestionDetails().find(
                      (itm) => itm.language === getDefaultLanguage()
                    )!.answers,
                  }}
                  wholeQuestion={
                    QuestionData() !== null
                      ? QuestionData()?.question.find(
                          (itm) => itm.language === getDefaultLanguage()
                        )
                      : undefined
                  }
                  answerQuestionFn={(answer: QuestionAnswersStore) => {
                    console.log("Sub ans: ", answer);
                    addAnswerFunction(answer.answers[0]);
                  }}
                  selectedLanguage={getDefaultLanguage()}
                  selectedExam={props.candidateExam.candidate_exam}
                />
              )}
          </div>
        </div>
      )}
    </div>
  );
};

const mapStateToProps = ({
  auth,
  candidateExam,
  system,
  questionTypes,
}: StoreState): {
  auth: Auth;
  candidateExam: candidateExamStoreInterface;
  system: System;
  questionTypes: questionTypesStoreInterface;
} => {
  return {
    auth: auth,
    candidateExam: candidateExam,
    system: system,
    questionTypes: questionTypes,
  };
};

const QuestionDetails = connect(mapStateToProps, {
  setSelectedExamQuestion,
  setQuestionLanguage,
  loadQuestionTypes,
})(_QuestionDetails);

export default QuestionDetails;
