interface DoExamProps {
  proctoring: Proctoring;
  FC_GetProfileDetails: Function;
  currentPosition: Interfaces.PositionAPI | null;
  doExam: examDataInterface;
  auth: Auth;
  loadExam: (user_id: string, exam: ExamInterface) => void;
  addExamAnswer: (questionAnswer: newAnswer) => void;
  deleteExamAnswers: () => void;
  SubmitExamAnswers: (user_id: string) => void;
  ChangeExamStatus: (user_id: string, status: examStatus) => void;
  candidateExam: candidateExamStoreInterface;
  setSelectedCandidateExam: (selected_exam: InitialExamDetails | null) => void;
  loadCandidateExam: (
    exam_id: string,
    CheckUserExam: (candidateExam: CandidateExam) => void,
    callback: (loading: boolean) => void
  ) => void;
  setSelectedExamQuestion: (
    exam_id: string,
    question_id: string | null
  ) => void;
  course: courseStoreInterface;
  loadCourses: (callback: (loading: boolean) => void) => void;
  periods: applicationPeriodsStoreInterface;
  setSelectedPeriod: (period: ApplicationPeriodGet) => void;
  setExamQuestionAnswer: (answer: QuestionAnswersStore) => void;
  questionTypes: questionTypesStoreInterface;
  loadQuestionTypes: (callback: (loading: boolean) => void) => void;
  RequestUserExamDetails: (
    userExamDetails: userExamDetails,
    callback: (loading: boolean) => void,
    setLoadExamResultMessage: (msg: string, status: boolean) => void,
    BackToExamination: (exam: ExamResultsDetails) => void
  ) => void;
  positions: positionsStoreInterface;
  PushPopupAlert: (alert: PopupAlertData) => void;
  CleanCandidateExam: (exam_id: string) => void;
  CleanCandidateExamAnswers: (exam_id: string) => void;
  SubmitExamQuestionAnswer: (
    answer: CandidateExamQuestionAnswer,
    callback: (loading: boolean) => void,
    actionStatus: (msg: string, status: boolean) => void
  ) => void;
  GetUserExamDetails: (
    request: RequestUserExamDetailsInterface,
    callback: (loading: boolean) => void
  ) => void;
  BackToExamination: (exam: ExamResultsDetails) => void;
  setExamStatus: (
    user: string,
    exam_paper_id: string,
    action: UserExamActions,
    reason: string,
    callback: (loading: boolean) => void
  ) => void;
  FC_SaveEvent: Function;
  socket: Socket;
  FC_AddRules: FT_AddRules;
  loadCandidateExamFromSupervisor: (
    candidateExam: CandidateExam,
    firstTime: boolean
  ) => void;
  requestUserAnswers: (
    exam_id: string,
    user_id: string,
    callback: (loading: boolean) => void
  ) => void;
}

interface DoExamState {
  windowStatus: {
    profile: boolean;
    questionsList: boolean;
  };
  selectedQuestion: exam_question | null;
  testAnswers: newAnswer[] | [];
  completedTest: boolean;
  examStatus: boolean;
  loading: boolean;
  loadUserExamErr: string;
  submitQuestionAnswer: boolean;
  submitWholeExam: boolean;
  display_exam: boolean;
  changingExamStatus: boolean;
  inExam: boolean;

  // Exam counter
  start_exam: boolean;
  start_time: string;
  duration: number;
  added_minutes: number;
  group_id: string;
  submittedExam: boolean;
  candidateDetailsProctoring: LiveServer__Candidate_Details | null;
  submitModalStatus: boolean;
}

export interface newAnswer {
  question_id: string | null;
  user_answers: question_answer | null;
}

class _DoEam extends Component<DoExamProps, DoExamState> {
  constructor(props: DoExamProps) {
    super(props);

    this.state = {
      windowStatus: {
        profile: false,
        questionsList: false,
      },
      selectedQuestion:
        this.props.doExam.exam_answers !== null
          ? this.props.doExam.exam_answers?.exam_questions[0]
          : null,
      testAnswers: [],
      completedTest: false,
      examStatus: false,
      loading: false,
      loadUserExamErr: "",
      submitQuestionAnswer: false,
      submitWholeExam: false,
      display_exam: false,
      changingExamStatus: false,
      // timer
      start_exam: false,
      start_time: "",
      duration: 0,
      added_minutes: 0,
      group_id: "",
      inExam: false,
      submittedExam: false,
      candidateDetailsProctoring: null,
      submitModalStatus: false,
    };
  }

  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };
  setSubmitQuestionAnswer = (status: boolean) => {
    this.setState({ submitQuestionAnswer: status });
  };

  setProfileWindowStatus = (status: boolean) => {
    this.setState({
      windowStatus: {
        profile: status,
        questionsList: this.state.windowStatus.questionsList,
      },
    });
  };
  setQuestionsListWindowStatus = (status: boolean) => {
    this.setState({
      windowStatus: {
        profile: this.state.windowStatus.profile,
        questionsList: status,
      },
    });
  };

  setSelectedQuestion = (question: exam_question) => {
    this.setState({ selectedQuestion: question });
  };

  answerQuestionFn = (answer: QuestionAnswersStore) => {
    console.log("sUBMITT: ", answer);
    this.props.setExamQuestionAnswer(answer);
    // console.log("Submitted: ", answer);
  };

  setCompletedTest = (state: boolean) => {
    this.setState({ completedTest: state });
  };

  changingExamStatusLoading = (status: boolean) => {
    this.setState({ changingExamStatus: status });
  };

  initializeEmptyList = () => {
    // localStorage.removeItem(ActionTypes.ADD_ANSWER)
    let testResults = localStorage.getItem(ActionTypes.ADD_ANSWER);
    if (testResults !== null) {
      let newData = JSON.parse(testResults!).questionAnswers;
      if (newData) {
        if (newData.length > 0) {
          for (let i in newData) {
            this.props.addExamAnswer(newData[i]);
          }
          if (
            newData.length ===
            this.props.doExam.exam_answers?.exam_questions.length
          ) {
            this.setState({ completedTest: true });
            this.props.SubmitExamAnswers(this.props.auth.user.n_id);
          }
        }
      }
    }
  };

  examDetails = () => {
    if (this.props.periods.selected_period !== null) {
      let check = this.props.candidateExam.selected_exam_details;
      if (check !== null) {
        return check;
      }
    }
    return null;
  };

  checkUserExam = (exam: CandidateExam) => {
    if (
      this.props.positions.selected_position !== null &&
      this.props.candidateExam.selected_exam_details !== null
    ) {
      if (this.props.candidateExam.exam_room_details !== null) {
        // alert(this.props.candidateExam.exam_room_details.district_id)
        this.props.RequestUserExamDetails(
          {
            user: this.props.auth.user.user_id,
            position: this.props.positions.selected_position.position_id,
            exam: exam,
            userExamLocation: {
              district_id:
                this.props.candidateExam.exam_room_details.district_id,
              center_id: this.props.candidateExam.exam_room_details.center_id,
              room_id: this.props.candidateExam.exam_room_details.room_id,
            },
          },
          this.setLoading,
          this.setLoadExamResultMessage,
          this.props.BackToExamination
        );
      }
    }
  };

  loadUserProfileDetails = () => {
    this.setState({
      loading: true,
    });
    this.props.FC_GetProfileDetails(
      this.props.auth.user.user_id,
      (status: boolean, msg: string) => {
        if (status === true) {
          this.setState({
            loading: false,
          });
        } else {
          this.setState({
            loading: false,
          });
        }
      }
    );
    this.setState({
      loading: false,
    });
  };

  componentDidMount = () => {
    if (
      this.props.auth.user.sex === "" ||
      this.props.auth.user.sex.length === 0
    ) {
      this.loadUserProfileDetails();
    }
    if (
      this.props.course.courses === null ||
      this.props.course.courses.length === 0
    ) {
      this.props.loadCourses(this.setLoading);
    }
    if (
      this.props.questionTypes.questionTypes === null ||
      this.props.questionTypes.questionTypes.length === 0
    ) {
      this.props.loadQuestionTypes(this.setLoading);
    }
    this.initializeEmptyList();
    if (
      this.props.doExam.exam_answers?.exam_questions.length === 0 &&
      this.props.doExam.exam_answers.exam_questions_answers &&
      this.props.doExam.exam_answers.exam_questions_answers?.questionAnswers
        .length > 0
    ) {
      this.setState({
        testAnswers:
          this.props.doExam.exam_answers.exam_questions_answers
            ?.questionAnswers,
      });
    }
    // Function to change exam status
  };

  componentWillUnmount = () => {
    if (this.props.candidateExam.exam_room_details) {
      this.props.socket.socket.emit(Events.USER_DISCONNECTED_ONLINE_SERVER, {
        room_id: this.props.candidateExam.exam_room_details.room_id,
        user_id: this.props.auth.user.user_id,
      });
    }
  };

  setLoadExamResultNoMessage = (msg: string, status: boolean) => {
    if (status === false) {
      this.props.PushPopupAlert({
        title: "Error!",
        description: msg,
        theme: PopupAlertTheme.danger,
      });
    }
  };

  setLoadExamResultMessage = (msg: string, status: boolean) => {
    this.props.PushPopupAlert({
      title: status === true ? "Success!" : "Error!",
      description: msg,
      theme: status === true ? PopupAlertTheme.success : PopupAlertTheme.danger,
    });
    if (status === false && this.props.candidateExam.candidate_exam !== null) {
      // );
      let test = this.props.candidateExam.user_exams_done.find(
        (itm) =>
          itm.exam_paper ===
          this.props.candidateExam.candidate_exam?.exam_paper_id
      );
      if (test === undefined) {
        this.props.CleanCandidateExam(
          this.props.candidateExam.candidate_exam.exam_paper_id
        );
      }
    }
  };

  SubmitAnswer = () => {
    if (
      this.props.candidateExam.candidate_exam !== null &&
      this.props.candidateExam.question_answers.length > 0
    ) {
      let examQuestionIds: string[] = [];
      for (let temp of this.props.candidateExam.candidate_exam.questions) {
        examQuestionIds = [...examQuestionIds, temp.question_id];
      }
      for (let answer of this.props.candidateExam.question_answers) {
        let sel_question =
          this.props.candidateExam.candidate_exam.questions.find(
            (item) => item.question_id === answer.question_id
          );
        if (
          answer.status === false &&
          this.props.candidateExam.exam_room_details &&
          sel_question !== undefined
        ) {
          if (
            questionAnswered(
              this.props.candidateExam.question_answers,
              sel_question,
              findQuestionType(
                sel_question.type,
                this.props.questionTypes.questionTypes
              )!
            ) === true
          ) {
            //Means updated need to be submitted!
            this.props.SubmitExamQuestionAnswer(
              {
                user: this.props.auth.user.user_id,
                exam_paper:
                  this.props.candidateExam.candidate_exam.exam_paper_id,
                // New updates need to be checked
                position:
                  this.props.candidateExam.exam_room_details.position
                    .position_id,
                disability: this.props.auth.user.disability,
                fname: this.props.auth.user.fname,
                lname: this.props.auth.user.lname,
                gender: this.props.auth.user.sex,
                email: this.props.auth.user.email,
                phone_number: this.props.auth.user.phone_number,
                in_education: this.props.auth.user.in_education,
                questions_order: examQuestionIds,
                // End new updates
                question: answer.question_id,
                language:
                  sel_question.question.find(
                    (itm) =>
                      itm.language ===
                      this.props.candidateExam.selected_language
                  ) === undefined
                    ? sel_question.question[0].language
                    : this.props.candidateExam.selected_language,
                userAnswer: answer.answers,
                position_code:
                  this.props.candidateExam.exam_room_details.position
                    .position_code,
              },
              this.setSubmitQuestionAnswer,
              this.setLoadExamResultNoMessage
            );
          }
        }
      }
    }
  };

  submitExam = async () => {
    if (
      this.props.candidateExam.candidate_exam !== null &&
      this.props.candidateExam.exam_room_details !== null
    ) {
      let exam_id = this.props.candidateExam.candidate_exam.exam_paper_id;
      let user_id = this.props.auth.user.user_id;
      this.setState({ submitWholeExam: true });
      try {
        const res = await axios.put(
          `${API.DO_EXAM}/answersheet/submit/${user_id}/${exam_id}`
        );
        if (res) {
          this.doneExam();
          this.setState({ submittedExam: true });
          // this.props.CleanCandidateExam(exam_id);
          // Exit full screen
          if (document.fullscreenElement) {
            if (document.exitFullscreen) {
              document.exitFullscreen();
            }
          }
          this.props.PushPopupAlert({
            title: "Success!",
            description: "Exam submitted successfully!",
            theme: PopupAlertTheme.success,
          });
        }
      } catch (error: any) {
        // console.log("Submit exam err: ", { ...error });
        this.props.PushPopupAlert({
          title: "Error occured!",
          description: errorToText(error),
          theme: PopupAlertTheme.danger,
        });
      }
      this.setState({ submitWholeExam: false });
    }
  };

  setSubmitModal = (status: boolean) => {
    this.setState({ submitModalStatus: status });
  };

  startExam = () => {
    this.setState({ display_exam: true, start_exam: true });
    this.props.candidateExam.selected_exam_id !== null &&
      this.requestExam(
        this.props.candidateExam.selected_exam_id,
        (status: boolean, exam: CandidateExam) => {
          // console.log("Testing exam from supervisor: ", exam);
          if (status === true) {
            this.props.loadCandidateExamFromSupervisor(
              exam,
              this.state.candidateDetailsProctoring !== null &&
                this.state.candidateDetailsProctoring.user.joined === true
                ? true
                : false
            );
          }
        }
      );
  };
  pauseExam = () => {
    this.setState({ display_exam: false, start_exam: false, inExam: false });
  };

  doneExam = () => {
    if (
      this.props.candidateExam.exam_room_details !== null &&
      this.props.candidateExam.candidate_exam !== null
    ) {
      let room_id = this.props.candidateExam.exam_room_details.room_id;

      let data = {
        application_session_id:
          this.props.auth.application_session?.session_plan_id,
        description: "Done the exam",
        done_by: "System",
        event_type_id: EventsLabels.DONE_EXAM,
        exam_id: this.props.candidateExam.candidate_exam.exam_paper_id, //this.props.proctoring.exam.
        position_id:
          this.props.candidateExam.exam_room_details.position.position_id,
        room_id: room_id,
        time: new Date().toString(),
        user_id: this.props.auth.user.user_id,
      } as EventInterface;

      this.props.FC_SaveEvent(data, (status: boolean, error: string) => {});
      this.props.socket.socket.emit(Events.EXAM_DONE_SERVER, data);
    }
  };

  timeOutExam = () => {
    if (
      this.props.candidateExam.exam_room_details !== null &&
      this.props.candidateExam.candidate_exam !== null
    ) {
      let room_id = this.props.candidateExam.exam_room_details.room_id;

      let data = {
        application_session_id:
          this.props.auth.application_session?.session_plan_id,
        description: "Time out",
        done_by: "System",
        event_type_id: EventsLabels.TIME_OUT,
        exam_id: this.props.candidateExam.candidate_exam.exam_paper_id, //this.props.proctoring.exam.
        position_id:
          this.props.candidateExam.exam_room_details.position.position_id,
        room_id: room_id,
        time: new Date().toString(),
        user_id: this.props.auth.user.user_id,
      } as EventInterface;

      this.props.FC_SaveEvent(data, (status: boolean, error: string) => {});
      this.props.socket.socket.emit(Events.TIME_OUT_SERVER, data);
      this.submitExam();
      // console.log("this.submitExam(); ==> timeOutExam()");
    }
  };

  requestExam = (
    exam_id: string,
    getExam: (status: boolean, exam: CandidateExam) => void
  ) => {
    this.props.socket.socket.emit(Events.REQUEST_EXAM_SERVER, exam_id, getExam);
  };

  render() {
    // Control if user has started the exam
    if (this.props.candidateExam.selected_exam_id === null) {
      return <Redirect to="/candidate-exams" />;
    }

    if (
      this.state.loading === true ||
      this.props.candidateExam.exam_room_details === null
    ) {
      return (
        <div className="fixed top-0 left-0 z-50 rounded px-4 py-4 bg-gray-200 w-full h-screen flex flex-col items-center justify-center">
          <div className="bg-white rounded mx-4 my-4 px-4 py-4 w-full h-full">
            <div className="text-gray-600 text-lg px-4 mb-4">
              Loading, Please wait...
            </div>
            <Loading />
          </div>
        </div>
      );
    }

    if (this.state.submitWholeExam === true) {
      return (
        <div className="fixed top-0 left-0 z-50 rounded px-4 py-4 bg-gray-200 w-full h-screen flex flex-col items-center justify-center">
          <div className="bg-white rounded mx-4 my-4 px-4 py-4 w-full h-full">
            <div className="text-gray-600 text-lg px-4 mb-4">
              Submitting your exam, Please wait...
            </div>
            <Loading />
          </div>
        </div>
      );
    }

    return (
      <div className="fixed top-0 left-0 z-50 w-full">
        <Fragment>
          {/* {console.log("Rules: ")} */}
          {this.state.inExam === false ? (
            <div className="fixed top-0 left-0 z-50 rounded px-4 py-4 bg-gray-200 w-full h-screen flex flex-col items-center justify-center">
              <div className="bg-white rounded mx-4 my-4 px-4 py-4 w-full h-full">
                <div className="text-white text-lg px-4 mb-4">Confirm</div>
                <div className="flex flex-col items-center justify-center mb-10">
                  <Loading />
                  <div className="text-yellow-800 text-xl font-bold my-5">
                    Something went wrong, retry again!
                  </div>

                  <div
                    onClick={() => window.location.reload()}
                    className="btn bg-yellow-100 text-yellow-800 border border-yellow-600 w-max text-center cursor-pointer"
                  >
                    Click here to reload
                  </div>
                </div>
              </div>
            </div>
          ) : (
            <ProctoringCandidateOnline
              startExam={() => {
                this.startExam();
                this.setState({ start_exam: true });
              }}
              restartExam={() => {
                if (this.props.candidateExam.candidate_exam !== null) {
                  let id =
                    this.props.candidateExam.candidate_exam.exam_paper_id;
                  this.props.CleanCandidateExamAnswers(id);

                  this.props.setExamStatus(
                    this.props.auth.user.user_id,
                    id,
                    UserExamActions.Restart,
                    "Exam restarted by supervisor",
                    this.changingExamStatusLoading
                  );

                  // this.props.candidateExam.candidate_exam &&
                  //   this.props.requestUserAnswers(
                  //     this.props.candidateExam.candidate_exam.exam_paper_id,
                  //     this.props.auth.user.user_id,
                  //     this.setLoading
                  //   );
                }
                this.startExam();
                this.setState({ start_exam: true });
                this.setState({ inExam: true });
              }}
              stopExam={() => {
                // EXAM DONE ON WHOLE ROOM
                // Stop all the room
                // this.setState({ start_exam: false });
                // console.log("DONE whole room");
                this.pauseExam();
                this.props.candidateExam.candidate_exam !== null &&
                  this.props.setExamStatus(
                    this.props.auth.user.user_id,
                    this.props.candidateExam.candidate_exam.exam_paper_id,
                    UserExamActions.Done,
                    "All candidates ended by supervisor",
                    this.changingExamStatusLoading
                  );
                this.props.candidateExam.candidate_exam !== null &&
                  this.props.CleanCandidateExam(
                    this.props.candidateExam.candidate_exam.exam_paper_id
                  );
              }}
              room_id={this.props.candidateExam.exam_room_details.room_id}
              exam_id={
                this.props.candidateExam.candidate_exam === null
                  ? this.props.candidateExam.selected_exam_id
                  : this.props.candidateExam.candidate_exam.exam_paper_id
              }
              position_id={
                this.props.candidateExam.exam_room_details.position.position_id
              }
              application_session_id={
                this.props.candidateExam.exam_room_details.period_id
              }
              removeClient={() => {
                // REMOVE INDIVIDUAL
                this.props.candidateExam.candidate_exam !== null &&
                  this.props.setExamStatus(
                    this.props.auth.user.user_id,
                    this.props.candidateExam.candidate_exam.exam_paper_id,
                    UserExamActions.Removed,
                    "Exam stopped by supervisor",
                    this.changingExamStatusLoading
                  );
                this.props.candidateExam.candidate_exam !== null &&
                  this.props.CleanCandidateExam(
                    this.props.candidateExam.candidate_exam.exam_paper_id
                  );
              }}
              examAlreadyDone={() => {
                // this.props.candidateExam.candidate_exam &&
                //   this.props.requestUserAnswers(
                //     this.props.candidateExam.candidate_exam.exam_paper_id,
                //     this.props.auth.user.user_id,
                //     this.setLoading
                //   );
                // console.log("Exam already done");
              }}
              candidateStopExam={(reason: string) => {
                // PAUSE
                // console.log(`candidateStopExam: ${reason}`);
                // this.pauseExam();
                // this.props.candidateExam.candidate_exam !== null &&
                //   this.props.setExamStatus(
                //     this.props.auth.user.user_id,
                //     this.props.candidateExam.candidate_exam.exam_paper_id,
                //     UserExamActions.Paused,
                //     reason,
                //     this.changingExamStatusLoading
                //   );
              }}
              candidateContinueExam={(reason: string) => {
                // console.log(`candidateContinueExam: ${reason}`);
                // this.props.candidateExam.candidate_exam !== null &&
                //   this.props.setExamStatus(
                //     this.props.auth.user.user_id,
                //     this.props.candidateExam.candidate_exam.exam_paper_id,
                //     UserExamActions.Continue,
                //     reason,
                //     this.changingExamStatusLoading
                //   );

                // this.props.candidateExam.candidate_exam &&
                //   this.props.requestUserAnswers(
                //     this.props.candidateExam.candidate_exam.exam_paper_id,
                //     this.props.auth.user.user_id,
                //     this.setLoading
                //   );
                this.startExam();
                this.setState({ start_exam: true });
                this.setState({ inExam: true });
              }}
              joinedRoom={(data: LiveServer__Room) => {
                // console.log("Joined room: ", data);
                this.setState({
                  group_id: data.group_id,
                });
              }}
              changeTime={(timer: {
                duration: number;
                start_time: string;
                added_minutes: string;
              }) => {
                this.setState({
                  duration: timer.duration,
                  start_time: timer.start_time,
                });
              }}
              candidateProctoringDetails={(
                data: LiveServer__Candidate_Details
              ) => {
                this.setState({ candidateDetailsProctoring: data });
                if (data.user.joined === true) {
                  this.startExam();
                  this.props.candidateExam.candidate_exam &&
                    this.props.requestUserAnswers(
                      this.props.candidateExam.candidate_exam.exam_paper_id,
                      this.props.auth.user.user_id,
                      this.setLoading
                    );
                }
                console.log("Joined: ", data.user.joined);
              }}
            >
              {this.state.start_exam === false ||
              (this.props.candidateExam.candidate_exam === null &&
                this.state.inExam === true) ? (
                // || this.props.proctoring.exam.start_exam === false
                <div className="fixed top-0 left-0 z-50 rounded px-4 py-4 bg-gray-200 w-full h-screen flex flex-col items-center justify-center">
                  <div className="bg-white rounded mx-4 my-4 px-4 py-4 w-full h-full flex flex-col items-center justify-center">
                    <MdInfoOutline className="text-9xl text-blue-600" />
                    <div className="text-gray-700 text-3xl px-4 text-center font-extrabold">
                      Please wait for the supervisor
                    </div>
                    <div className="text-gray-700 text-sm px-4 mb-4 text-center">
                      The supervisor will allow you to start your exam, please
                      wait or contact him/her when forgot to let you start exam!
                    </div>

                    <Loading />
                    <div className="flex flex-row items-center -mt-2">
                      <div
                        onClick={() => window.location.reload()}
                        className="btn bg-blue-100 text-blue-600 cursor-pointer hover:bg-blue-200 hover:text-blue-808"
                      >
                        Reload page
                      </div>
                    </div>
                  </div>
                </div>
              ) : this.props.candidateExam.candidate_exam === null ||
                this.props.candidateExam.candidate_exam === null ||
                this.props.proctoring.exam.start_exam === false ? (
                <div className="fixed top-0 left-0 z-50 rounded px-4 py-4 bg-gray-200 w-full h-screen flex flex-col items-center justify-center">
                  <div className="bg-white rounded mx-4 my-4 px-4 py-4 w-full h-full flex flex-col items-center justify-center">
                    <MdInfoOutline className="text-9xl text-blue-600" />
                    <div className="text-gray-700 text-3xl px-4 text-center font-extrabold">
                      Please wait for the supervisor
                    </div>
                    <div className="text-gray-700 text-sm px-4 mb-4 text-center">
                      The supervisor will allow you to start your exam, please
                      wait or contact him/her when forgot to let you start exam!
                    </div>

                    <Loading />
                    <div className="flex flex-row items-center -mt-2">
                      <div
                        onClick={() => window.location.reload()}
                        className="btn bg-blue-100 text-blue-600 cursor-pointer hover:bg-blue-200 hover:text-blue-808"
                      >
                        Reload page
                      </div>
                    </div>
                  </div>
                </div>
              ) : (
                <div>
                  <div className="fixed top-0 w-full h-screen bg-blue-900 overflow-y-hidden">
                    <div className="grid md:grid-cols-12 h-full">
                      {this.state.windowStatus.profile === true && (
                        <div
                          className={`col-span-6 lg:col-span-2 fixed left-0 md:relative`}
                        >
                          <Profile
                            setProfileWindowStatus={this.setProfileWindowStatus}
                          />
                        </div>
                      )}
                      <div
                        className={`col-span-${
                          this.state.windowStatus.profile === false
                            ? "12"
                            : "10"
                        } bg-gray-800 shadow-lg h-full`}
                      >
                        <div className="flex flex-row h-screen bg-gray-800">
                          <div className="h-full">
                            {/* Questions list */}
                            <div className={`h-full`}>
                              <QuestionsList
                                windowStatus={this.state.windowStatus}
                                setProfileWindowStatus={
                                  this.setProfileWindowStatus
                                }
                                setQuestionsListWindowStatus={
                                  this.setQuestionsListWindowStatus
                                }
                                questionTypes={this.props.questionTypes}
                                loadQuestionTypes={this.props.loadQuestionTypes}
                                SubmitAnswer={this.SubmitAnswer}
                              />
                            </div>
                          </div>
                          <div className="w-full h-full">
                            {/* Do exam test title */}
                            <DoExamTitle
                              submitModalStatus={this.state.submitModalStatus}
                              setSubmitModal={this.setSubmitModal}
                              setCompletedTest={this.setCompletedTest}
                              windowStatus={this.state.windowStatus}
                              setProfileWindowStatus={
                                this.setProfileWindowStatus
                              }
                              setQuestionsListWindowStatus={
                                this.setQuestionsListWindowStatus
                              }
                              questionTypes={this.props.questionTypes}
                              loadQuestionTypes={this.props.loadQuestionTypes}
                              submitQuestionAnswer={
                                this.state.submitQuestionAnswer
                              }
                              submitExam={() => {
                                this.submitExam();
                                // console.log(
                                //   "this.submitExam() ===> <DoExamTitle />"
                                // );
                              }}
                              selectedExam={this.props.course.selected_course}
                              SubmitAnswer={this.SubmitAnswer}
                              start_time={this.state.start_time}
                              duration_min={this.state.duration}
                              timeout={() => this.timeOutExam()}
                            />
                            {/* Question details */}
                            <QuestionDetails
                              questionAnswers={
                                this.props.doExam.exam_answers
                                  ?.exam_questions_answers?.questionAnswers
                              }
                              answerQuestionFn={this.answerQuestionFn}
                              selectedQuestion={this.state.selectedQuestion}
                              setSelectedQuestion={this.setSelectedQuestion}
                              preview={false}
                              submitQuestionAnswer={
                                this.state.submitQuestionAnswer
                              }
                              doExam={this.props.doExam}
                            />
                          </div>
                        </div>
                      </div>
                    </div>
                    <Footer
                      setSubmitModal={this.setSubmitModal}
                      windowStatus={this.state.windowStatus}
                      setProfileWindowStatus={this.setProfileWindowStatus}
                      setQuestionsListWindowStatus={
                        this.setQuestionsListWindowStatus
                      }
                      questionTypes={this.props.questionTypes}
                      loadQuestionTypes={this.props.loadQuestionTypes}
                      SubmitAnswer={this.SubmitAnswer}
                      submitQuestionAnswer={this.state.submitQuestionAnswer}
                    />
                  </div>
                </div>
              )}
            </ProctoringCandidateOnline>
          )}
          {this.props.candidateExam.candidate_exam === null &&
            this.state.inExam === false && (
              <Modal
                title=""
                backDrop={true}
                theme={Themes.default}
                close={() => this.props.setSelectedCandidateExam(null)}
                backDropClose={false}
                widthSizeClass={ModalSize.extraLarge}
                displayClose={false}
                padding={{ title: false }}
              >
                <div className="rounded px-4 py-4 mx-2 my-2">
                  {this.props.course.courses === null ? (
                    <Loading />
                  ) : (
                    <div>
                      <div className="flex flex-row items-center gap-2">
                        {/* <div className="flex items-center justify-center ">
                        <IoIosPaper className="text-9xl text-blue-600" />
                      </div> */}
                        <div className="my-2 w-full">
                          <div className="text-blue-600 text-2xl font-extrabold mb-2">
                            {this.props.course.selected_course && (
                              <div>
                                <span>
                                  {
                                    this.props.course.selected_course
                                      .course_code
                                  }
                                  -
                                </span>
                                <span>
                                  {
                                    this.props.course.selected_course
                                      .course_name
                                  }
                                </span>
                              </div>
                            )}
                          </div>
                          <div className="flex flex-row gap-3 text-sm">
                            <span className="text-gray-600 font-normal">
                              Exam date:
                            </span>
                            <span className="text-blue-600 font-bold">
                              {this.examDetails() &&
                                DATE(
                                  this.examDetails()!.exam_date,
                                  "DD/MM/YYYY"
                                )}
                            </span>
                          </div>
                          <div className="flex flex-row gap-3 text-sm">
                            <span className="text-gray-600 font-normal">
                              Start time:
                            </span>
                            <span className="text-blue-600 font-bold">
                              {this.examDetails()?.start_time}
                            </span>
                          </div>
                          <div className="flex flex-row gap-3 text-sm">
                            <span className="text-gray-600 font-normal">
                              Exam duration:
                            </span>
                            {this.examDetails() !== null && (
                              <span className="text-blue-600 font-bold">
                                {this.examDetails()!.duration / 60 >= 1 &&
                                  this.examDetails()!.duration / 60 +
                                    " hrs"}{" "}
                                {this.examDetails()!.duration % 60 > 0 && (
                                  <span>
                                    {this.examDetails()!.duration % 60} mins
                                  </span>
                                )}
                              </span>
                            )}
                          </div>
                        </div>
                      </div>
                      {/* <div className="text-blue-600 text-3xl font-extrabold text-center">Start Exam</div> */}
                      <div className="flex flex-row justify-center items-center">
                        {/* <div className="text-blue-600 text-xl font-extrabold text-center bg-blue-50 rounded px-2 py-2 w-max">00:00:00</div> */}
                      </div>
                      <div className="text-gray-600 font-bold text-l text-center mb-4">
                        <div className="rounded px-4 py-4 bg-gray-100 my-4 text-sm text-left font-normal">
                          <div className="text-left text-gray-800 text-xl mb-3 font-extrabold">
                            Examination rules
                          </div>
                          {this.examDetails() !== null && (
                            <div
                              dangerouslySetInnerHTML={{
                                __html: this.examDetails()!.termes,
                              }}
                            ></div>
                          )}
                        </div>
                        <div className="rounded px-4 py-4 bg-gray-100 my-4 text-sm text-left font-normal">
                          <div className="text-left text-gray-800 text-xl mb-3 font-extrabold">
                            Exam Proctoring rules
                          </div>
                          {this.examDetails() !== null && (
                            <>
                              <ProctoringRuleItem
                                examDetails={this.examDetails}
                                rule_key="right_click_enabled"
                              />
                              <ProctoringRuleItem
                                examDetails={this.examDetails}
                                rule_key="copy_enabled"
                              />
                              <ProctoringRuleItem
                                examDetails={this.examDetails}
                                rule_key="paste_enabled"
                              />
                              <ProctoringRuleItem
                                examDetails={this.examDetails}
                                rule_key="stop_candidate_when_comeback"
                              />
                              <ProctoringRuleItem
                                examDetails={this.examDetails}
                                rule_key="allow_to_leave_browser"
                              />
                            </>
                          )}
                        </div>
                        <span className="text-blue-700">
                          If you are ready, click to start the exam now, The
                          system will start to count your time!
                        </span>
                      </div>
                      <div className="flex flex-row items-center justify-between mt-2">
                        <button
                          onClick={() =>
                            this.props.setSelectedCandidateExam(null)
                          }
                          className="btn bg-gray-50 text-gray-600 border"
                        >
                          Go Back
                        </button>
                        <button
                          onClick={() => {
                            this.setState({ inExam: true });
                            this.props.candidateExam.selected_exam_id !==
                              null &&
                              this.requestExam(
                                this.props.candidateExam.selected_exam_id,
                                (status: boolean, exam: CandidateExam) => {
                                  if (status === true) {
                                    this.props.loadCandidateExamFromSupervisor(
                                      exam,
                                      this.state.candidateDetailsProctoring !==
                                        null &&
                                        this.state.candidateDetailsProctoring
                                          .user.joined === true
                                        ? true
                                        : false
                                    );
                                  }
                                }
                              );
                          }}
                          className="btn bg-blue-600 text-white"
                        >
                          I accept, Start exam
                        </button>
                      </div>
                    </div>
                  )}
                </div>
              </Modal>
            )}
          {this.state.submittedExam === true && (
            <Modal
              title=""
              backDrop={true}
              theme={Themes.default}
              close={() => this.props.setSelectedCandidateExam(null)}
              backDropClose={false}
              widthSizeClass={ModalSize.medium}
              displayClose={false}
              padding={{ title: false }}
            >
              <div className="px-5 py-5">
                <div className="flex flex-row items-center justify-center">
                  <MdCheckCircle className="text-7xl text-blue-600" />
                </div>
                <div className="flex flex-row items-center justify-center">
                  <span className="text-3xl font-extrabold text-blue-600 text-center">
                    Exam submitted
                  </span>
                </div>
                <div className="flex flex-row items-center justify-center mt-3 mb-5">
                  <span className="text-center text-gray-500">
                    Thank you for attending the exam, Your exam has been
                    submitted successfully!, click continue to the applications
                    list, you will open the application to view your score right
                    now.
                  </span>
                </div>
                <div className="flex flex-row items-center justify-center">
                  <div
                    onClick={() => {
                      this.setState({ submittedExam: false });
                      if (document.fullscreenElement) {
                        if (document.exitFullscreen) {
                          document.exitFullscreen();
                        }
                      }
                      this.props.candidateExam.candidate_exam &&
                        this.props.CleanCandidateExam(
                          this.props.candidateExam.candidate_exam.exam_paper_id
                        );
                      window.location.reload();
                    }}
                    className="btn bg-blue-100 text-blue-700 rounded w-max cursor-pointer"
                  >
                    Continue to view marks
                  </div>
                </div>
              </div>
            </Modal>
          )}
        </Fragment>
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  doExam,
  candidateExam,
  system,
  course,
  periods,
  questionTypes,
  positions,
  socket,
  proctoring,
}: StoreState): {
  auth: Auth;
  doExam: examDataInterface;
  candidateExam: candidateExamStoreInterface;
  system: System;
  course: courseStoreInterface;
  periods: applicationPeriodsStoreInterface;
  questionTypes: questionTypesStoreInterface;
  positions: positionsStoreInterface;
  socket: Socket;
  proctoring: Proctoring;
} => {
  return {
    auth: auth,
    doExam: doExam,
    candidateExam: candidateExam,
    system: system,
    course: course,
    periods: periods,
    questionTypes: questionTypes,
    positions: positions,
    socket,
    proctoring,
  };
};

export const DoExam = connect(mapStateToProps, {
  loadExam,
  addExamAnswer,
  deleteExamAnswers,
  SubmitExamAnswers,
  ChangeExamStatus,
  setSelectedCandidateExam,
  loadCandidateExam,
  setSelectedExamQuestion,
  loadCourses,
  setSelectedPeriod,
  setExamQuestionAnswer,
  loadQuestionTypes,
  RequestUserExamDetails,
  PushPopupAlert,
  CleanCandidateExam,
  SubmitExamQuestionAnswer,
  GetUserExamDetails,
  BackToExamination,
  setExamStatus,
  FC_SaveEvent,
  FC_AddRules,
  CleanCandidateExamAnswers,
  loadCandidateExamFromSupervisor,
  requestUserAnswers,
  FC_GetProfileDetails,
})(_DoEam);
